[data-foo]::after{
    content: attr(data-foo) " ";
}

[aria-selected="true"]{
    display: none;
    animation-name: zoom2;
    animation-duration: .3s;
    animation-delay: 0s;
    animation-direction: alternate;
    animation-iteration-count: 1;
}
[aria-selected="false"]{ 
	display: block;
	animation-name: zoom;
    animation-duration: .3s;
    animation-delay: 0s;
    animation-direction: alternate;
    animation-iteration-count: 1;
 }
