$height-100: 100%;

$width-100: 100%;

$max-width: 1120px;

$yugothic-font-path: "../../font/YuGothic";

$header-height: 70px;

$spaceamounts: (1, 2, 4, 8, 16, 32, 64, 78);

$font-size: (
	'xs' : 12px,
	'sm' : 14px,
	'md:' : 16px,
	'lg' : 18px,
	'xl' : 20px,
	'xxl' : 25px
);

$z-index: (
	"mobileStepper": 1000,
	"speedDial": 1050,
	"appBar": 1100,
	"drawer": 1200,
	"modal": 1300,
	"snackbar": 1400,
	"tooltip": 1500
);

$fontFamily: (
	"-apple-system",
	"BlinkMacSystemFont",
	"Segoe UI",
	"Roboto",
	"Helvetica Neue",
	"Arial",
	"sans-serif",
	"Apple Color Emoji",
	"Segoe UI Emoji",
	"Segoe UI Symbol"
);

$color-collection: (
    White:#ffffff, 
    Black:#1d1d1e, 
    Blue:#0054a7,
    Green:#4bbc56,
    Red:#d32f2f,
    Gray:#6a5b42
);
$palette: (
	common: (
		black: #000000,
		white: #ffffff
	),
	primary: (
		light: #7986cb,
		main: #3f51b5,
		dark: #303f9f
	),
	secondary: (
		light: #ff4081,
		main: #f50057,
		dark: #c51162
	),
	error: (
		light: #e57373,
		main:#f44336,
		dark: #d32f2f
	),
	warning: (
		light: #ffb74d,
		main: #ff9800,
		dark: #f57c00
	),
	info: (
		light: #64b5f6,
		main: #2196f3,
		dark: #1976d2
	),
	success: (
		light: #81c784,
		main: #4caf50,
		dark: #388e3c
	),
	grey: (
		50: #fafafa,
		100: #f5f5f5,
		200: #eeeeee,
		300: #e0e0e0,
		400: #bdbdbd,
		500: #9e9e9e,
		600: #757575,
		700: #616161,
		800: #424242,
		900: #212121,
		A100: #d5d5d5,
		A200: #aaaaaa,
		A400: #303030,
		A700: #616161
	)
);
$sides: (
	"": "",
	"t": "top",
	"b": "bottom",
	"l": "left",
	"r": "right",
);

$breakpoints: (
	"": "",
	"xs": 576px,
	"sm": 768px,
	"md": 992px,
	"lg": 1200px,
);

$icons: (
	"eye": f112, 
	"start": f12e, 
	"stop": f12f
);
$value:1/10;
