.list-item {
    @include flex(center,center);
    .item {
        border-right: 1px solid #8e8e8e;
        .block-el {
            display: block;
            padding: 1.5rem 1rem 1.5rem 1rem;
            a {
                text-decoration: none;
                color: #8e8e8e;
                text-transform: uppercase;
                font-size: 0.7rem;
            }
        }
        &:last-child {
            border: none;
        }
    }
}
