@mixin clearfix　{
    &:after {
		content: "";
		display: table;
		clear: both;
    }
}
@mixin fontsizevw($input,$base:1100) {
    font-size: ($input/ $base) *1vw;
}
@mixin screen($size){
	@each $breakName, $breakValue in $breakpoints {
		@if $breakName != "" {
			@if $size == $breakValue {
				@media only screen and #{$breakValue} {
					@content;
				}
			}
			@else {
				@media only screen and #{$size} {
					@content;
				}
			}
		}
	}
}
@mixin background($url,$height) {
    background-image: url('../img/'+$url);
    background-position: center;
    background-size: cover;
    height: $height;
}
@mixin flex($alName,$jusName) {
    display: flex;
    align-items: $alName;
    justify-content: $jusName;
}
@mixin keyframes($name) {
    @-webkit-keyframes #{$name} {
    	@content;
    }
  
    @-moz-keyframes #{$name} {
    	@content;
    }
  
    @keyframes #{$name} {
		@content;
    }
}
@mixin renderColor($colorArray) {
  @each $name,$color in $colorArray {
      .color#{"" + $name}{
        color: $color;
      }
  }
}
@mixin renderBgColor($colorArray) {
  @each $name,$color in $colorArray{
    .bg#{"" + $name}{
    	background-color: $color;
  	}
}
}
@mixin btn-theme($bgColor) {
	background-color: $bgColor;
	border-color: darken($color: $bgColor, $amount: 20%);
	&:hover {
		background-color: lighten($color: $bgColor, $amount: 90%);
	}
	&:active {
		box-shadow: darken($bgColor, 20%); 
	}
}
// @include trans
@mixin trans($val) {
	-webkit-transition: $val;
	-moz-transition: $val; 
	-o-transition: $val; 
	transition: $val; 
}

// @include icons
@mixin icons($icons) {
	@each $name,$glyph in $icons {
		.icon-#{$name}:before {
			display: inline-block;
			font-family: "Icon Font";
			content: "#{$glyph}";
		}
	}
}

// @include response above
@mixin response-above($breakpoint) {
	@if map-has-key($breakpoints, $breakpoint) {
		$breakpoint-value:map-get($breakpoints, $breakpoint);
		@media (min-width: $breakpoint-value) {
			@content;
		}
	}
	@else {
		@warn 'Invalid break point: #{$breakpoint}.';
	}
}

// @include response below
@mixin response-below($breakpoint) {
	@if map-has-key($breakpoints, $breakpoint) {
		$breakpoint-value:map-get($breakpoints, $breakpoint);
		@media (min-width: ($breakpoint-value -1)) {
			@content;
		}
	}
	@else {
		@warn 'Invalid break point: #{$breakpoint}.';
	}
}

// @include response between
@mixin response-between($lower,$upper) {
	@if map-has-key($breakpoints, $lower) and map-has-key($breakpoints, $upper) {
		$lower-breakpoint:map-get($breakpoints, $lower);
		$upper-breakpoint:map-get($breakpoints, $upper);
		@media (min-width: $lower-breakpoint) and (max-width: ($upper-breakpoint-1)) {
			@content;
		}
	}
	@else {
		@if (map-has-key($breakpoints, $lower) == false) {
			@warn 'Your lower breakpoint was Invalid: #{$lower}.';
		}
		@if (map-has-key($breakpoints, $upper) == false) {
			@warn 'Your upper breakpoint was Invalid: #{$upper}.';
		}
	}
}
