.header {
    display: flex;
    height: $header-height;
    position: fixed;
    width: 100%;
    .wrapper_menu {
        display: flex;
        align-items: flex-start;
        background-color: #fff;
        opacity: 0.7;
        height: 70px;
        width: 100%;
    }
    .nav {
        margin-left: 20px;
        height: 100%;
      }
      .nav > ul {
        height: 100%;
        display: flex;
        align-items: center;
        margin: 0;
      }
      .nav .item {
        margin-left: 50px;
      }
      .list-sub-item {
        position: absolute;
        background-color: #ffffff;
        padding: 0 20px 20px;
      }
      .sub-item {
        padding: 15px 30px;
        border-bottom: 1px solid;
      }
}
.banner {
	background-image: url(http://demo.posthemes.com/pos_ecolife/layout2/modules/posslideshows/images/081b9c1b802e10a8438349ca084ec5451f955c8e_2_2.jpg);
	background-position: center;
	width: 100%;
	height: 500px;
}  
