.btn_toggle{
    display: none;
    width: 40px;
    height: 33px;
    padding-top: 1rem;
    .toggle{
        width: 100%;
        cursor: pointer;
        .one,.two,.three{
            width: 100%;
            height: 3px;
            background: #333333;
            margin: 6px auto;
            backface-visibility: hidden;
            transition-duration: 0.5s;
        }
        .one{
            transform: rotate(45deg) translate(6px, 7px);
        }
        .two{
            opacity: 0;
        }
        .three{
            transform: rotate(-45deg) translate(6px, -7px);
        }
    }
}
.shopping-box{
    width: 4.723rem;
    position: fixed;
    bottom: 12rem;
    right: 4rem;
    button{
        overflow: hidden;
        border-radius: 50%;
        border: none;
        background-color: transparent;
        cursor: pointer;
    }
    #js-show-counts{
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        font-size: 1rem;
        font-weight: bold;
        color: red;
        .counts{
            background-color: beige;
        }
    }
}
