@each $breakName, $breakValue in $breakpoints {
    @each $sideName, $sideValue in $sides {
      	@for $i from 0 through 4 {
			$property: if($sideName == '', '', -#{$sideValue});
			$space: $i * 10;
			$selector: '';
			@if $breakName != "" {
				$selector: #{$sideName}-#{$breakName}-#{$i};
			} @else {
				$selector: #{$sideName}-#{$i};
			}
	
			@if $breakName != "" {
			@media (min-width: $breakValue) {
				.m#{$selector} {
					margin#{$property}: #{$space}px;
					margin#{$property}: #{$space*$value}rem;
				}

				.p#{$selector} {
					padding#{$property}: #{$space}px;
					padding#{$property}: #{$space*$value}rem;
				}
			}
			} 
			@else {
				.m#{$selector} {
					margin#{$property}: #{$space}px;
					margin#{$property}: #{$space*$value}rem;
				}
		
				.p#{$selector} {
					padding#{$property}: #{$space}px;
					padding#{$property}: #{$space*$value}rem;
				}
			}
    	}
    }
}

