.footer {
	background-color: #62c6f8;
	color: map-get($map: $color-collection, $key: White);
	font-size: 0.5rem;
	&-wrapper {
		padding: 2rem 0 1.5rem;
		@include flex(unset, space-between);
		.footer-content {
			@include flex(flex-start, space-between);
			flex-direction: column;
			&:not(:last-child) {
				.footer-content-item {
					&::before{
					content: "";
					display: inline-block;
					background-color: map-get($map: $color-collection, $key: White);
					width: 6px;
					height: 6px;
					border-radius: 3px;
					margin-right: 13px;
					}
				}
			}
			.footer-content-title {
				font-size: 0.7rem;
				text-transform: uppercase;
				font-weight: bold;
			}
			.footer-content-item {
				text-transform: uppercase;
				@include flex(center,flex-start);
				img {
					width: 1rem;
				}
			}
		}
	}
	.footer-border {
		display: block;
		border-bottom: 1px solid #ffffff;
	}
	.footer-author {
		@include flex(center,space-between);
		flex-direction: row;
		.footer-author-item {
			font-size: 12px;
			text-transform: uppercase;
		span {
			color: #71DEE2;
		}
		#js-copy-right-year {
			color: #ffffff;
		}
		}
	}
}
