
/* MARGIN AND PADDING */
@each $space in $spaceamounts{
	@each $sideName,$sideValue in $sides {
		@if $sideName != '' {
			.m-#{$sideName}-#{$space} {
				margin-#{$sideValue}: #{$space}px;
				margin-#{$sideValue}: #{$space*$value}rem;
			}
			.p-#{$sideName}-#{$space} {
				padding-#{$sideValue}: #{$space}px;
				margin-#{$sideValue}: #{$space*$value}rem;
			}
		}
	}
}
